// !Please check the node_modules/bootstrap/scss/_variables.scss file from Bootstrap before styling, as you can overwrite variables here first for cleaner CSS!

// COLORS
// If you need white or black, just use $white or $black, Bootstrap already has the variables assigned.

// MODULES (Generic)
$spacer: 2.4rem;
$negative-spacer: -2.4rem;
$no-padding: 0;
$no-margin: 0;

// TYPOGRAPHY

// Base, body
// $body-bg: $white;
// $body-color: $color-2;
$font-family-sans-serif: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif;
$font-family-base: "Your Font Here", $font-family-sans-serif;
$font-size-base: 1.6rem; // 1.6 x 10 (html font-size) = 16px
$line-height-lg: 2.5; // Default $line-height-base is 1.5
$full-width: 100%;
$small-font-size: 1.2rem;

// Headings
$h1-font-size: 3.6rem; // Homepage h1 will need different styling
$h2-font-size: 2.4rem;
$h3-font-size: 1.8rem;
$h4-font-size: $font-size-base;
$h5-font-size: $font-size-base;
$h6-font-size: $font-size-base;
